html,
body,
#root {
  height: 100%;
}
.container {
  text-align: left;
  display: flex;
  flex-direction: column;
  height: 100%;
}
header {
  padding: 20px 20px 0;
}
h2 {
  margin: 0;
  font-size: 1.5em;
}
h1 {
  font-weight: 700;
  margin: 0;
  font-size: 3em;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.body {
  flex: 1;
  overflow: auto;
  padding: 20px;
}
.imageWrapper {
  margin-bottom: 60px;
}
.image {
  max-width: 600px;
  width: 100%;
  height: auto;
}
.imageCopy {
  padding: 12px 0;
}
.imageCopy p {
  margin: 0;
}
.imageCopy .year {
  font-weight: 700;
}
.imageCopy .dimensions {
  font-style: italic;
  font-size: 14px;
}
.nav {
  display: flex;
  align-items: center;
}
.yearHeader {
  padding: 12px 0;
}
.nav button,
.nav a,
.statement button {
  cursor: pointer;
  background: transparent;
  border: none;
  font-size: 1em;
  color: blue;
  padding: 0;
  margin-left: 6px;
  text-decoration: underline;
  font-family:
    -apple-system, "system-ui", "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}
.nav button:hover,
.nav a:hover,
.statement button:hover {
  font-weight: 700;
}
.statement {
  background: white;
  padding: 30px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
}
.statementHeader {
  padding: 12px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.statement h1 {
  margin: 0;
  font-size: 1.5em;
}
.statement p {
  font-size: 1.1em;
  max-width: 680px;
  line-height: 1.5;
  margin-bottom: 24px;
}
